// Imports
import { Fragment, useState } from "react";
import { useMediaQuery } from 'react-responsive'
import uniqid from 'uniqid';
import { marked } from 'marked';
import Select, { components } from 'react-select';
import Modal from "./modal";
import Tooltip from "rc-tooltip";
import "rc-tooltip/assets/bootstrap_white.css";
import SyntaxHighlighter from 'react-syntax-highlighter';
import {  
  atelierForestDark,
  atelierForestLight,
  atelierHeathDark,
  atelierHeathLight,
  atelierLakesideDark,
  atelierLakesideLight,
  atelierPlateauDark,
  atelierPlateauLight,
  atelierSavannaDark,
  atelierSavannaLight,
  atelierSeasideDark,
  atelierSeasideLight,
  atelierSulphurpoolDark,
  atelierSulphurpoolLight,
  atomOneDarkReasonable,
  atomOneDark,
  atomOneLight,
  brownPaper,
  codepenEmbed,
  colorBrewer,
  darcula,
  dark,
  defaultStyle,
  docco,
  dracula,
  far,
  foundation,
  githubGist,
  github,
  gml,
  googlecode,
  gradientDark,
  gradientLight,
  grayscale,
  gruvboxDark,
  gruvboxLight,
  hopscotch,
  hybrid,
  idea,
  irBlack,
  isblEditorDark,
  isblEditorLight,
  kimbieDark,
  kimbieLight,
  lightfair,
  lioshi,
  magula,
  monoBlue,
  monokaiSublime,
  monokai,
  nightOwl,
  nnfxDark,
  nnfx,
  nord,
  obsidian,
  ocean,
  paraisoDark,
  paraisoLight,
  pojoaque,
  purebasic,
  qtcreatorDark,
  qtcreatorLight,
  railscasts,
  rainbow,
  routeros,
  schoolBook,
  shadesOfPurple,
  solarizedDark,
  solarizedLight,
  srcery,
  stackoverflowDark,
  stackoverflowLight,
  sunburst,
  tomorrowNightBlue,
  tomorrowNightBright,
  tomorrowNightEighties,
  tomorrowNight,
  tomorrow,
  vs,
  vs2015,
  xcode,
  xt256,
  zenburn
} from 'react-syntax-highlighter/dist/esm/styles/hljs';

const MainContainer = (props) => {
  /* 
  linkArray[] - has the links to show on navigation
  codeArray[] - has the code in string format and also some other meta data
  */
  const {linkArray, codeArray, config} = props;
  /* 
  codeIndex is used to change 
  the current code screen by 
  indexing the codeArray
  */
  const [codeIndex, setCodeIndex] = useState(0);
  // To store react <Select /> value
  const [searchVal, setSearchVal] = useState(null);
  // This is a boolean value to show/unshow example's
  const [exampleModal, setExampleModal] = useState(false);
  const isTabletOrMobile = useMediaQuery({maxDeviceWidth: 1200}) 
  /* 
  codeArray[codeIndex].datetime contains
  the epoc time in MilliSec format. This is
  converted to Js Date() object
  */
  const codeDate = new Date(Number(codeArray[codeIndex].datetime));
  // Date Format Options
  const dateOptions = {
    year:"numeric", 
    month:"short", 
    day: "numeric"
  }; 
  // This is the formatted version of the Date() object
  const codeDateFormatted = codeDate?.toLocaleDateString('en-us', dateOptions);
  /* 
  This function returns month 
  difference between today's date
  and when the code file was written 
  It returns an emplty string if 
  difference was found to be zero or less 
  */
  const getMonthDiff = (startDate = codeDate, endDate = new Date()) => {
    const monthDiff = endDate.getMonth() - startDate.getMonth() +
    12 * (endDate.getFullYear() - startDate.getFullYear());
    if (monthDiff <= 0) return "";
    return `(${monthDiff} Month${monthDiff > 1 ? 's' : null} ago)`;
  }
  // React <Select /> Customized Options field
  const Option = ({ children, ...props }) => {
    console.log(props);
    const statusStyles = {color: props.data?.status ? "#BC1B14" : "#01B201"};
    return (
      <components.Option {...props}>
        <span>{children} &nbsp;
          <span style={statusStyles}>&bull;</span>
        </span>
      </components.Option>
    )
  }

  return (
    <Fragment>
      {/* Examples Modal */}
      <Modal 
        isOpen={exampleModal} 
        onClose={()=>setExampleModal(false)}>
        {
          Array.from(Array(10).keys()).map(idx => {
            const exArray = codeArray[codeIndex][`example${idx+1}`];
            if (exArray) {
              return (
                <Fragment>
                <p data-role="mono-font">
                  Example {idx+1}
                </p>
                <ul data-role="mono-font">
                  {
                    exArray?.map(example => 
                      <li className="">{example}</li>
                    )
                  }
                </ul>
                </Fragment>
              )
            }
          })
        }
      </Modal>

      <div className="col-sm-12 col-xl-9 order-2 order-xl-1">
        {/* 
          Tooltip showing when the code 
          file was written and the month difference
          between today and when it was written  
        */}
        <Tooltip 
          placement="bottom" 
          trigger={["hover"]} 
          overlay={
            <span data-role="mono-font"> 
              Submitted on { `${codeDateFormatted} ${getMonthDiff()}` } 
            </span>
          }>
          {/* Problem Main Heading */}
          <a
            href={codeArray[codeIndex].prob_link}
            target="_blank"
            rel="noopener noreferrer"
            className="h2 pb-2 d-inline text-decoration-none text-dark text-break"
            style={{cursor: "pointer"}}>
              {codeArray[codeIndex].prob_name?.join(" ") ?? codeArray[codeIndex].prob_filename}
          </a>
        </Tooltip>

        {/* The code problem question */}
        <p className="lh-lg mt-3 mb-4" data-role="mono-font">
          {codeArray[codeIndex].prob_qs?.join(" ")}
        </p>

        {/* This is the Button Navigator / Tooltip buttons below the question */}
        { 
          config.prob_buttons ?
          (<div className="btn-toolbar mb-3">
            {
              // Examples Button
              codeArray[codeIndex].example1 ?
              (<button
                className="btn btn-sm btn-outline-dark m-1" 
                onClick={() => setExampleModal(true)}>Examples</button>) : null
            }
            {
              // Constraints Tooltip Button
              codeArray[codeIndex].constraint ?
              (<Tooltip
                placement="right" 
                trigger={["hover", "click"]} 
                overlay={
                  <Fragment>
                    <p className="h6">Constraints</p>
                    <ul data-role="mono-font">
                      {
                        codeArray[codeIndex].constraint?.map(constraint => 
                          <li>{constraint}</li>  
                        )
                      }
                    </ul>
                  </Fragment>
                }>
                <button
                  className="btn btn-sm btn-outline-danger m-1">Constraints</button>
              </Tooltip>) : null
            }
            {
              // Performance Tooltip Button
              codeArray[codeIndex].prob_perf ?
              (<Tooltip 
                placement="right" 
                trigger={["hover", "click"]} 
                overlay={
                  <Fragment>
                    <p className="h6">Performance</p>
                    <ul data-role="mono-font">
                      {
                        codeArray[codeIndex].prob_perf?.map(perf => 
                          <li>{perf}</li>  
                        )
                      }
                    </ul>
                  </Fragment>
                }>
                <button
                  className="btn btn-sm btn-outline-primary m-1">Performance</button>
              </Tooltip>) : null
            }
            {
              // Success / Failure of code Button
              config.success_default_btn ?
              (<button
                className={`btn btn-sm btn-outline-${linkArray[codeIndex].status ? "danger" : "success"} m-1`}>
                {linkArray[codeIndex].status ?? "Accepted"}
              </button>) : null
            }
          </div>) : null
        }

        {/* Code */}
        <SyntaxHighlighter
          className="rounded"
          language={codeArray[codeIndex].prob_lang}
          showLineNumbers
          codeTagProps = {{
            style: {
              fontFamily: "monolisa-rg",
              fontSize: "1rem"
            }
          }}
          style={monokaiSublime}>
          {codeArray[codeIndex].prob_code?.join("\n")}
        </SyntaxHighlighter>

        {/* Solution Explained */}
        <div
          className="my-5"
          dangerouslySetInnerHTML={{ __html: codeArray[codeIndex].prob_solution }} 
          data-role="mono-font"/>
      </div>

      {/* Navigation Links Division */}
      <div className={`col-sm-12 col-xl-3 px-3 overflow-${isTabletOrMobile ? 'visible': 'hidden'} order-1 order-xl-2`} style={{maxHeight: "600px"}}>
        <p className="d-none d-xl-block">Problem list :</p>

        <div className="w-100 h-auto pb-3">
          <Select
            placeholder="Search"
            value={searchVal}
            components={{ Option }}
            options={
              linkArray?.map((link, index)=>{
                return {value: index, label: link.text, status: link.status};
              })
            } 
            onChange={(obj)=>{
              setSearchVal(obj);
              setCodeIndex(obj?.value)
            }}
          />
        </div>
        
        <ol type="1" className="overflow-scroll pt-2 pb-5 h-100 d-none d-xl-block">
          {
            linkArray?.map((link, index)=>{
              const statusStyles = {color: link.status ? "#BC1B14" : "#01B201"};

              return (
                <li key={uniqid()}>
                  <span
                    role="button"
                    className="link-primary text-decoration-none text-dark text-break"
                    onClick={()=> setCodeIndex(index)}>{link.text} &nbsp;
                      <span style={statusStyles}>&bull;</span>  
                  </span>
                </li>
              )
            })
          }
        </ol>
      </div>
    </Fragment>
  )
}

export default MainContainer;