// Imports
import { useEffect } from "react";
import { useMediaQuery } from 'react-responsive'

const Modal = (props) => {
  const {
    isOpen,
    onClose,
    children,
  } = props;

  const smallScreens = useMediaQuery({ maxWidth: "768px" });

  useEffect(()=>{
    if(isOpen) {
      let bodyStyles = document.body.style;
      bodyStyles.overflowY = "hidden";

      return () => {
        bodyStyles.overflowY = "auto";
      }
    }
  }, [isOpen]);

  const modalStyles = {
    bg: {
      zIndex: 999,
      position: "fixed",
      width: "100vw",
      height: "100vh",
      top: 0,
      left: 0,
      backgroundColor: "rgba(0, 0, 0, 0.6)",
    },
  
    fg: {
      position: "absolute",
      width: smallScreens ? "90%" : "700px",
      height: "500px",
      top: "50%",
      left: "50%",
      padding: "30px",
      transform: "translate(-50%, -50%)",
      backgroundColor: "white",
    },

    content: {
      height: "100%",
      overflowY: "auto",
      borderBottom: "1px dotted black"
    },

    closeBtn: {
      border: "none",
      outline: "none",
      borderRadius: "50%",
      width: "26px",
      height: "26px",
      top: "20px",
      right: "20px",
      backgroundColor: "#F5544D",
      color: "#BC1B14",
      position: "absolute",
      fontSize: "0.8rem",
    }
  };

  if (isOpen) {
    return (
      <div 
        className="container-fluid"
        data-modal-close
        onClick={({target}) => target.dataset?.modalClose ? onClose() : null}
        style={modalStyles.bg}>
        <div 
          className="rounded" 
          style={modalStyles.fg}>
          <button
            onClick={onClose} 
            style={modalStyles.closeBtn}>X</button>
          
          <div 
            className="container py-3"
            style={modalStyles.content}>
            {children}
          </div>

        </div>
      </div>
    )
  }
}

export default Modal;